import React, { useEffect, useRef, useState } from 'react';
import { Link } from 'react-router-dom';
import { b, base, mainUrl, uri, url } from '../../api/const';
import { useFetch } from '../../api/useFetch';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Pagination } from 'swiper';
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/navigation';
import '../../index.css';
import { ClipLoader } from 'react-spinners';
import SanitizedHTML from 'react-sanitized-html';
import { useTranslation } from 'react-i18next';
import pattern from '../../assets/image/home/Frame 3678.png';

const Banner = () => {
  const swiperRef = useRef();
  const { isLoading, response } = useFetch(base + mainUrl + '/slider/');
  // const [response, setResponse] = useState([])
  const { i18n } = useTranslation();
  //base + mainUrl + '/slider/'

  // const getSliders = async () => {
  //     const res = await axios.get(base + mainUrl + '/slider/')
  //     setResponse(res.data)
  // }
  //
  // useEffect(() => {
  //     getSliders()
  // },[])

  if (isLoading) {
    return (
      <div role="status" className="flex justify-center my-28 pb-24">
        <ClipLoader color="#1985A1" size={300} />
      </div>
    );
  }

  return (
    <div className="relative">
      <Swiper
        hashNavigation={{
          watchState: true,
        }}
        onSwiper={(swiper) => {
          swiperRef.current = swiper;
        }}
        loop={true}
        slidesPerView={1}
        navigation={{
          nextEl: '.banner-next',
          prevEl: '.banner-prev',
        }}
        modules={[Pagination, Navigation]}
        className="mySwiper"
      >
        {response &&
          response.map((item) => (
            <SwiperSlide key={item.id}>
              <div className="flex flex-wrap justify-center w-full h-[481px] bg-arrow">
                <img
                  src={pattern}
                  alt=""
                  className="w-full h-[481px] object-cover bg-no-repeat absolute right-0 top-0 z-10"
                />
                <img
                  src={uri + item.background_image}
                  alt=""
                  className="w-[60%] h-[481px] bg-no-repeat absolute left-0 top-0"
                />
                <div className="w-[4.3%] h-[624px] bg-gradient-banner opacity-[40%] absolute top-0 left-0 z-20"></div>
                <div className="w-[4.3%] h-[624px] absolute bg-gradient-banner opacity-[40%] top-0 right-0 z-20"></div>
                <div className="container w-[1236px] h-[481px] m-auto text-white bg-gradient-banner opacity-[100%] absolute top-0 flex justify-end pr-12 z-10">
                  {i18n.language === 'ky' && (
                    <div className="self-center w-[363px] ">
                      <p className="text-[30px] font-bold w-[400px]">
                        <SanitizedHTML html={item.title} />
                      </p>
                      <p className="text-[32px] font-normal"></p>
                      <p className="text-[32px] font-normal"></p>
                      <p className="mt-[26px] font-normal text-[26px]">
                        <SanitizedHTML html={item.subtitle_ky} />
                      </p>
                    </div>
                  )}
                  {i18n.language === 'ru' && (
                    <div className="self-center w-[363px]">
                      <p className="text-[30px] font-bold w-[400px]">
                        <SanitizedHTML html={item.title} />
                      </p>
                      <p className="text-[32px] font-normal"></p>
                      <p className="text-[32px] font-normal"></p>
                      <p className="mt-[26px] font-normal text-[26px]">
                        <SanitizedHTML html={item.subtitle_ru} />
                      </p>
                    </div>
                  )}
                  {i18n.language === 'en' && (
                    <div className="self-center w-[363px]">
                      <p className="text-[32px] font-bold w-[400px]">
                        <SanitizedHTML html={item.title} />
                      </p>
                      <p className="text-[32px] font-normal"></p>
                      <p className="text-[32px] font-normal"></p>
                      <p className="mt-[26px] font-normal text-[26px]">
                        <SanitizedHTML html={item.subtitle_en} />
                      </p>
                    </div>
                  )}
                </div>
              </div>
            </SwiperSlide>
          ))}
      </Swiper>
      <div
        className="banner-prev absolute top-[220px] left-[20px]"
        onClick={() => swiperRef.current.slidePrev()}
      ></div>
      <div
        className="banner-next absolute top-[220px] right-[25px]"
        onClick={() => swiperRef.current.slideNext()}
      ></div>
    </div>
  );
};

export default Banner;
