import React, { Component, useState } from 'react';
import { Link, NavLink } from 'react-router-dom';
import search from '../../assets/image/main/search-icon.png';
import Social from './social';
import close from '../../assets/image/main/close.png';
import { useNavigate } from 'react-router';
import { useTranslation } from 'react-i18next';
import { Language } from './Language';
import logo from '../../assets/image/home/Group.svg';

const Menu = () => {
  const navigate = useNavigate();
  const [openModal, setOpenModal] = useState(false);
  const { t } = useTranslation();

  const toggleModal = () => {
    setOpenModal(!openModal);
  };

  const handleChange = (val) => {
    localStorage.setItem('search', val);
    navigate('/search');
  };

  return (
    <div className="relative mb-0">
      {!openModal ? (
        <div className="bg-gradient-menu">
          <div className="wrapper h-[115px] flex justify-between items-center font-inter cursor-pointer">
            <img src={logo} alt="kelechek" onClick={() => navigate('/')} />
            {/*<About/>*/}
            {/*<CoreAdvice/>*/}
            <NavLink
              to="/news"
              className="text-white text-[15px] font-semibold"
            >
              {t('about')}
            </NavLink>
            <NavLink
              to="/category/1"
              className="text-white text-[15px] font-semibold"
            >
              {t('history')}
            </NavLink>
            <NavLink
              to="/gallery"
              className="text-white text-[15px] font-semibold "
            >
              {t('people')}
            </NavLink>
            <NavLink
              to="/category/1"
              className="text-white text-[15px] font-semibold"
            >
              {t('generation')}
            </NavLink>
            <Language />
            <img
              src={search}
              alt="search"
              className="w-[20px] h-[20px] cursor-pointer"
              onClick={toggleModal}
            />
            <Social />
          </div>
        </div>
      ) : (
        <div className="w-full h-[115px] bg-blueLight z-10">
          <div className="wrapper py-[32px]">
            <input
              type="text"
              autoFocus={true}
              className="bg-blueLight border-none outline-none w-[98.5%] h-[24px] font-medium text-[15px]"
              placeholder=""
              onKeyDown={(e) =>
                e.key === 'Enter' && handleChange(e.target.value)
              }
            />
            <img
              src={close}
              alt="close"
              className="py-2 cursor-pointer"
              onClick={toggleModal}
            />
          </div>
        </div>
      )}
    </div>
  );
};

export default Menu;
